'use client'

import { useState, useRef, useMemo, useEffect } from "react"
import { motion, useInView } from "framer-motion"
import Link from "next/link"
import Image from "next/image"
import { useRouter, useSearchParams, usePathname } from "next/navigation"
import ProductCard from "@/components/ProductCard"

interface CategoryListingClientProps {
  category: any
  subcategories: any[]
  products: any[]
  totalProducts: number
  availableRooms: any[]
  availableFeatures: string[]
  initialSearchParams: {
    page?: string
    sort?: string
    subcategory?: string
    rooms?: string
    features?: string
  }
}

export default function CategoryListingClient({
  category,
  subcategories,
  products: initialProducts,
  totalProducts,
  availableRooms,
  availableFeatures,
  initialSearchParams
}: CategoryListingClientProps) {
  const router = useRouter()
  const pathname = usePathname()
  const searchParams = useSearchParams()

  const heroRef = useRef(null)
  const productsRef = useRef(null)
  const isHeroInView = useInView(heroRef, { once: true, amount: 0.3 })
  const isProductsInView = useInView(productsRef, { once: true, amount: 0.1 })

  // Filter states
  const [selectedSubcategories, setSelectedSubcategories] = useState<string[]>(
    initialSearchParams.subcategory?.split(',').filter(Boolean) || []
  )
  const [selectedRooms, setSelectedRooms] = useState<string[]>(
    initialSearchParams.rooms?.split(',').filter(Boolean) || []
  )
  const [selectedFeatures, setSelectedFeatures] = useState<string[]>(
    initialSearchParams.features?.split(',').filter(Boolean) || []
  )
  const [sortBy, setSortBy] = useState(initialSearchParams.sort || "popular")
  const [viewMode, setViewMode] = useState<'grid' | 'list'>("grid")
  const [currentPage, setCurrentPage] = useState(
    parseInt(initialSearchParams.page || '1')
  )

  const productsPerPage = 12

  // Update URL when filters change
  useEffect(() => {
    const params = new URLSearchParams()

    if (selectedSubcategories.length > 0) {
      params.set('subcategory', selectedSubcategories.join(','))
    }
    if (selectedRooms.length > 0) {
      params.set('rooms', selectedRooms.join(','))
    }
    if (selectedFeatures.length > 0) {
      params.set('features', selectedFeatures.join(','))
    }
    if (sortBy !== 'popular') {
      params.set('sort', sortBy)
    }
    if (currentPage > 1) {
      params.set('page', currentPage.toString())
    }

    const queryString = params.toString()
    const newUrl = queryString ? `${pathname}?${queryString}` : pathname

    router.push(newUrl, { scroll: false })
  }, [selectedSubcategories, selectedRooms, selectedFeatures, sortBy, currentPage, pathname, router])

  // Client-side filtering and sorting
  const filteredAndSortedProducts = useMemo(() => {
    let filtered = [...initialProducts]

    // Filter by subcategory
    if (selectedSubcategories.length > 0) {
      filtered = filtered.filter(product =>
        selectedSubcategories.includes(product.category?.slug)
      )
    }

    // Filter by rooms
    if (selectedRooms.length > 0) {
      filtered = filtered.filter(product =>
        product.product_rooms?.some((pr: any) =>
          selectedRooms.includes(pr.room?.slug)
        )
      )
    }

    // Filter by features
    if (selectedFeatures.length > 0) {
      filtered = filtered.filter(product =>
        selectedFeatures.every(feature =>
          product.product_features?.some((f: any) =>
            f.feature_name.toLowerCase() === feature.toLowerCase()
          )
        )
      )
    }

    // Sort products
    switch (sortBy) {
      case 'newest':
        filtered.sort((a, b) => new Date(b.created_at).getTime() - new Date(a.created_at).getTime())
        break
      case 'name-asc':
        filtered.sort((a, b) => a.name.localeCompare(b.name))
        break
      case 'name-desc':
        filtered.sort((a, b) => b.name.localeCompare(a.name))
        break
      case 'popular':
      default:
        // Featured items first, then new items
        filtered.sort((a, b) => {
          if (a.is_featured && !b.is_featured) return -1
          if (!a.is_featured && b.is_featured) return 1
          if (a.is_new && !b.is_new) return -1
          if (!a.is_new && b.is_new) return 1
          return 0
        })
    }

    return filtered
  }, [initialProducts, selectedSubcategories, selectedRooms, selectedFeatures, sortBy])

  // Pagination
  const totalFilteredProducts = filteredAndSortedProducts.length
  const totalPages = Math.ceil(totalFilteredProducts / productsPerPage)
  const startIndex = (currentPage - 1) * productsPerPage
  const endIndex = startIndex + productsPerPage
  const currentProducts = filteredAndSortedProducts.slice(startIndex, endIndex)

  // Handle filter changes
  const toggleSubcategory = (slug: string) => {
    setSelectedSubcategories((prev) =>
      prev.includes(slug) ? prev.filter((s) => s !== slug) : [...prev, slug]
    )
    setCurrentPage(1)
  }

  const toggleRoom = (slug: string) => {
    setSelectedRooms((prev) =>
      prev.includes(slug) ? prev.filter((r) => r !== slug) : [...prev, slug]
    )
    setCurrentPage(1)
  }

  const toggleFeature = (feature: string) => {
    setSelectedFeatures((prev) =>
      prev.includes(feature) ? prev.filter((f) => f !== feature) : [...prev, feature]
    )
    setCurrentPage(1)
  }

  const clearAllFilters = () => {
    setSelectedSubcategories([])
    setSelectedRooms([])
    setSelectedFeatures([])
    setCurrentPage(1)
  }

  const hasActiveFilters =
    selectedSubcategories.length > 0 ||
    selectedRooms.length > 0 ||
    selectedFeatures.length > 0

  return (
    <div className="w-full">
      {/* Hero Section */}
      <section
        ref={heroRef}
        className="relative h-[85vh] min-h-[400px] flex items-center justify-center px-4 sm:px-6 md:px-12 lg:px-24 xl:px-44 text-white overflow-hidden"
      >
        {/* Background Image */}
        {category.image_url && (
          <Image
            src={category.image_url}
            alt={category.name}
            fill
            className="object-cover"
            priority
          />
        )}

        {/* Dark Overlay */}
        <div className="absolute inset-0 bg-gradient-to-t from-black/70 via-black/30 to-black/40" />

        {/* Breadcrumb */}
        <div className="absolute top-24 left-4 sm:left-6 md:left-12 lg:left-24 xl:left-44 z-10">
          <div className="flex items-center gap-2 text-sm text-white/80">
            <Link href="/" className="hover:text-white transition-colors">
              Home
            </Link>
            <span>/</span>
            <Link href="/furniture" className="hover:text-white transition-colors">
              Furniture
            </Link>
            <span>/</span>
            <span className="text-white font-semibold">{category.name}</span>
          </div>
        </div>

        {/* Content */}
        <div className="relative z-10 text-center max-w-4xl">
          <motion.span
            initial={{ opacity: 0, y: 20 }}
            animate={isHeroInView ? { opacity: 1, y: 0 } : {}}
            transition={{ duration: 0.6, ease: [0.22, 1, 0.36, 1] }}
            className="inline-block text-sm font-bold uppercase tracking-widest text-red-400 mb-4"
          >
            Browse Category
          </motion.span>

          <motion.h1
            initial={{ opacity: 0, y: 30 }}
            animate={isHeroInView ? { opacity: 1, y: 0 } : {}}
            transition={{ duration: 0.8, ease: [0.22, 1, 0.36, 1], delay: 0.1 }}
            className="text-4xl sm:text-5xl md:text-6xl lg:text-7xl font-black leading-tight mb-4"
          >
            {category.name}
          </motion.h1>

          <motion.div
            initial={{ scaleX: 0 }}
            animate={isHeroInView ? { scaleX: 1 } : {}}
            transition={{ duration: 1, ease: [0.22, 1, 0.36, 1], delay: 0.2 }}
            className="h-1 bg-gradient-to-r from-red-500 to-red-300 w-32 mx-auto mb-6"
          />

          <motion.p
            initial={{ opacity: 0, y: 20 }}
            animate={isHeroInView ? { opacity: 1, y: 0 } : {}}
            transition={{ duration: 0.7, ease: [0.22, 1, 0.36, 1], delay: 0.3 }}
            className="text-lg md:text-xl text-white/90 leading-relaxed"
          >
            {category.description}
          </motion.p>
        </div>
      </section>

      {/* Subcategory Pills */}
      {subcategories.length > 0 && (
        <section className="bg-white border-b-2 border-gray-100 py-6 px-4 sm:px-6 md:px-12 lg:px-24 xl:px-44 sticky top-0 z-40 shadow-sm">
          <div className="flex items-center gap-3 overflow-x-auto pb-2 scrollbar-hide">
            <span className="text-sm font-semibold text-gray-600 whitespace-nowrap">
              Filter:
            </span>
            <button
              onClick={clearAllFilters}
              className={`px-4 py-2 rounded-full text-sm font-semibold transition-all duration-300 whitespace-nowrap ${
                !hasActiveFilters
                  ? "bg-gradient-to-r from-red-900 to-red-700 text-white"
                  : "bg-gray-100 text-gray-700 hover:bg-gray-200"
              }`}
            >
              All {category.name}
            </button>
            {subcategories.map((sub) => (
              <button
                key={sub.slug}
                onClick={() => toggleSubcategory(sub.slug)}
                className={`px-4 py-2 rounded-full text-sm font-semibold transition-all duration-300 whitespace-nowrap ${
                  selectedSubcategories.includes(sub.slug)
                    ? "bg-gradient-to-r from-red-900 to-red-700 text-white"
                    : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                }`}
              >
                {sub.name}
              </button>
            ))}
          </div>
        </section>
      )}

      {/* Main Content: Filters + Products */}
      <section className="px-4 sm:px-6 md:px-12 lg:px-24 xl:px-44 py-12 bg-gray-50">
        <div className="flex flex-col lg:flex-row gap-8">
          {/* Sidebar Filters */}
          <aside className="lg:w-72 flex-shrink-0">
            <div className="bg-white border-2 border-gray-200 p-6 sticky top-32">
              <div className="flex items-center justify-between mb-6">
                <h3 className="text-lg font-bold uppercase tracking-wider">
                  Filters
                </h3>
                {hasActiveFilters && (
                  <button
                    onClick={clearAllFilters}
                    className="text-sm text-red-700 hover:text-red-900 font-semibold"
                  >
                    Clear All
                  </button>
                )}
              </div>

              {/* Room Type Filter */}
              {availableRooms.length > 0 && (
                <>
                  <div className="mb-8">
                    <h4 className="text-sm font-bold uppercase tracking-wider text-gray-700 mb-3">
                      Room Type
                    </h4>
                    <div className="space-y-2">
                      {availableRooms.map((room) => (
                        <label
                          key={room.slug}
                          className="flex items-center gap-3 cursor-pointer group"
                        >
                          <input
                            type="checkbox"
                            checked={selectedRooms.includes(room.slug)}
                            onChange={() => toggleRoom(room.slug)}
                            className="w-4 h-4 text-red-700 border-gray-300 rounded focus:ring-red-500"
                          />
                          <span className="text-sm text-gray-700 group-hover:text-red-700 transition-colors">
                            {room.name}
                          </span>
                        </label>
                      ))}
                    </div>
                  </div>

                  <div className="h-px bg-gray-200 mb-8" />
                </>
              )}

              {/* Features Filter */}
              {availableFeatures.length > 0 && (
                <div className="mb-8">
                  <h4 className="text-sm font-bold uppercase tracking-wider text-gray-700 mb-3">
                    Features
                  </h4>
                  <div className="space-y-2">
                    {availableFeatures.map((feature) => (
                      <label
                        key={feature}
                        className="flex items-center gap-3 cursor-pointer group"
                      >
                        <input
                          type="checkbox"
                          checked={selectedFeatures.includes(feature)}
                          onChange={() => toggleFeature(feature)}
                          className="w-4 h-4 text-red-700 border-gray-300 rounded focus:ring-red-500"
                        />
                        <span className="text-sm text-gray-700 group-hover:text-red-700 transition-colors">
                          {feature}
                        </span>
                      </label>
                    ))}
                  </div>
                </div>
              )}

              {/* CTA */}
              <div className="pt-6 border-t-2 border-gray-200">
                <Link
                  href="/contact"
                  className="w-full px-6 py-3 bg-black text-white text-sm font-semibold uppercase tracking-wider hover:bg-gray-800 transition-all duration-300 flex items-center justify-center gap-2"
                >
                  Need Help?
                </Link>
              </div>
            </div>
          </aside>

          {/* Products Grid */}
          <main ref={productsRef} className="flex-1">
            {/* Toolbar */}
            <div className="flex flex-col sm:flex-row items-start sm:items-center justify-between gap-4 mb-8">
              {/* Results Count */}
              <p className="text-sm text-gray-600">
                Showing{" "}
                <span className="font-semibold text-gray-900">
                  {totalFilteredProducts > 0 ? startIndex + 1 : 0}-{Math.min(endIndex, totalFilteredProducts)}
                </span>{" "}
                of <span className="font-semibold text-gray-900">{totalFilteredProducts}</span>{" "}
                products
              </p>

              {/* Sort & View */}
              <div className="flex items-center gap-4">
                {/* Sort Dropdown */}
                <select
                  value={sortBy}
                  onChange={(e) => setSortBy(e.target.value)}
                  className="px-4 py-2 border-2 border-gray-200 rounded text-sm font-semibold text-gray-700 focus:outline-none focus:border-red-700 transition-colors"
                >
                  <option value="popular">Most Popular</option>
                  <option value="newest">Newest First</option>
                  <option value="name-asc">Name: A-Z</option>
                  <option value="name-desc">Name: Z-A</option>
                </select>

                {/* View Toggle */}
                <div className="hidden md:flex items-center gap-2 bg-white border-2 border-gray-200 rounded p-1">
                  <button
                    onClick={() => setViewMode("grid")}
                    className={`p-2 rounded transition-colors ${
                      viewMode === "grid"
                        ? "bg-red-700 text-white"
                        : "text-gray-400 hover:text-gray-700"
                    }`}
                    aria-label="Grid view"
                  >
                    <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 24 24">
                      <path d="M3 3h8v8H3V3zm10 0h8v8h-8V3zM3 13h8v8H3v-8zm10 0h8v8h-8v-8z" />
                    </svg>
                  </button>
                  <button
                    onClick={() => setViewMode("list")}
                    className={`p-2 rounded transition-colors ${
                      viewMode === "list"
                        ? "bg-red-700 text-white"
                        : "text-gray-400 hover:text-gray-700"
                    }`}
                    aria-label="List view"
                  >
                    <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 24 24">
                      <path d="M3 4h18v2H3V4zm0 7h18v2H3v-2zm0 7h18v2H3v-2z" />
                    </svg>
                  </button>
                </div>
              </div>
            </div>

            {/* Products Grid */}
            {currentProducts.length > 0 ? (
              <div
                className={
                  viewMode === "grid"
                    ? "grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8"
                    : "flex flex-col gap-6"
                }
              >
                {currentProducts.map((product, index) => (
                  <ProductCard
                    key={product.id}
                    product={product}
                    index={index}
                    isInView={isProductsInView}
                  />
                ))}
              </div>
            ) : (
              <div className="text-center py-16">
                <svg
                  className="w-24 h-24 mx-auto text-gray-300 mb-4"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={1.5}
                    d="M9.172 16.172a4 4 0 015.656 0M9 10h.01M15 10h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z"
                  />
                </svg>
                <h3 className="text-xl font-bold text-gray-700 mb-2">No products found</h3>
                <p className="text-gray-500 mb-6">
                  Try adjusting your filters to find what you're looking for.
                </p>
                <button
                  onClick={clearAllFilters}
                  className="px-6 py-3 bg-gradient-to-r from-red-900 to-red-700 text-white font-semibold uppercase tracking-wider hover:from-red-800 hover:to-red-600 transition-all duration-300"
                >
                  Clear All Filters
                </button>
              </div>
            )}

            {/* Pagination */}
            {totalPages > 1 && (
              <div className="flex items-center justify-center gap-2 mt-12">
                <button
                  onClick={() => setCurrentPage((p) => Math.max(1, p - 1))}
                  disabled={currentPage === 1}
                  className="px-4 py-2 border-2 border-gray-200 hover:border-red-700 hover:text-red-700 disabled:opacity-30 disabled:cursor-not-allowed transition-all"
                >
                  Previous
                </button>

                {Array.from({ length: Math.min(totalPages, 5) }, (_, i) => {
                  let page
                  if (totalPages <= 5) {
                    page = i + 1
                  } else if (currentPage <= 3) {
                    page = i + 1
                  } else if (currentPage >= totalPages - 2) {
                    page = totalPages - 4 + i
                  } else {
                    page = currentPage - 2 + i
                  }

                  return (
                    <button
                      key={page}
                      onClick={() => setCurrentPage(page)}
                      className={`w-10 h-10 border-2 font-semibold transition-all ${
                        currentPage === page
                          ? "bg-gradient-to-r from-red-900 to-red-700 text-white border-red-700"
                          : "border-gray-200 hover:border-red-700 hover:text-red-700"
                      }`}
                    >
                      {page}
                    </button>
                  )
                })}

                <button
                  onClick={() => setCurrentPage((p) => Math.min(totalPages, p + 1))}
                  disabled={currentPage === totalPages}
                  className="px-4 py-2 border-2 border-gray-200 hover:border-red-700 hover:text-red-700 disabled:opacity-30 disabled:cursor-not-allowed transition-all"
                >
                  Next
                </button>
              </div>
            )}
          </main>
        </div>
      </section>

      {/* Related Categories CTA */}
      <section className="px-4 sm:px-6 md:px-12 lg:px-24 xl:px-44 py-16 bg-gradient-to-br from-gray-900 via-red-950 to-black text-white">
        <div className="max-w-4xl mx-auto text-center">
          <h2 className="text-3xl md:text-4xl font-bold mb-4">
            Explore More Categories
          </h2>
          <p className="text-lg text-white/80 mb-8">
            Can't find what you're looking for? Browse our other furniture categories
            or contact our experts for personalized recommendations.
          </p>
          <div className="flex flex-col sm:flex-row gap-4 justify-center">
            <Link
              href="/furniture"
              className="px-8 py-4 bg-white text-black font-semibold uppercase tracking-wider hover:bg-gray-100 transition-all duration-300"
            >
              View All Categories
            </Link>
            <Link
              href="/contact"
              className="px-8 py-4 bg-transparent border-2 border-white text-white font-semibold uppercase tracking-wider hover:bg-white hover:text-black transition-all duration-300"
            >
              Contact Experts
            </Link>
          </div>
        </div>
      </section>
    </div>
  )
}
